// INPUTS
[type="text"],
[type="tel"],
[type="submit"] {
  position: relative;
  border: 0;
  padding: 0;
  margin: 0;
  width: 100%; }

::placeholder {
  color: $placeholder-color; }


.fill, .dropdown {
  position: relative;
  height: 32px;
  padding: 0 10px;
  @include input-border; }

.dropdown {
  -webkit-appearance: none;
  -moz-appearance: none;
  cursor: pointer;
  display: inline-block;
  position: relative;
  min-width: 64px;

  background-image: url(/assets/icons/select-arrow.svg);
  background-size: 9px 5px;
  background-position: 90% center;
  background-repeat: no-repeat; }

.dropdown__split {
  width: 20px;
  height: 32px;
  line-height: 32px;
  position: relative;
  text-align: center;
  vertical-align: middle; }
