// JS-TRIGGERSE TOOLTIP WITH ERRORS
@import "variables";

.valid {
  border: 1px solid rgb(60,179,113);
  color: rgb(60,179,113);
  font-size: 12px;
  background-color: rgba(60,179,113,0.05);
  @include drop-shadow-md;
  z-index: 999;
  transition: ease 0.25s; }

.grid__col label.error {
  left: 0;
  top: -32px;
  padding: 5px 10px;
  position: absolute;
  white-space: nowrap;
  font-weight: bold;
  font-size: 12px;
  color: $red-alert-border;
  background-color: $red-alert;
  border: 1px solid $red-alert-border;
  border-radius: 3px;
  @include drop-shadow-md;
  z-index: 999;
  @media #{$desktop} {
    left: 50%;
    transform: translateX(-50%); } }
